@import url(https://cdn.jsdelivr.net/npm/bootstrap@4.6.0/dist/css/bootstrap.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css);
header{
    width: 100%;
    height: 100px;
    background-color:#EC1D24;
    display: flex;
    align-items: center;
    justify-content: center;

}

nav {

    width: 95%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-family: 'Marvel', sans-serif;
}

.logo{
    height: 100px;
}

.header-actions{
    display: flex;
    align-items: center;
    justify-content: space-between;

    min-width: 20%;
}

.total-hq{
    font-size: 1.5rem;
    color: #fff;
}

.button-header{
    font-size: 1.2rem;
    padding: 0 2rem;
    height: 2rem;
    background-color: #EC1D24;
    border: 2px solid #fff; 
    color: #fff;
    border-radius: 0.5rem;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: 0.5s;
}

.button-header:hover{
    color:  #EC1D24;
    background-color: #fff;
    border: 2px solid #EC1D24
}
.search-comic {
    height: 70px;
    display: block;
    padding: 10px;
    font-size: 20px;
    border-radius: 5px;
    width: 40%;
    margin: 20px auto;
}
.modal-comic{
    background-color: #fff;
    border-radius: 4px;
    width: 800px;
    margin: 80px auto 0 auto;
    padding: 15px;
}

.modal__overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0, 0.5);

}

.modal__close-button{
    float: right;
    font-size: 1.4rem;
    width: 2rem;
    height: 2rem;
    background: none;
    border: unset; 
    color: #000;
    border-radius: 0.5rem;
    cursor:pointer;
}

.imgComic-modal{
    height: 600px;
    width: 400px;
    display: flex;
    margin:auto ;
}

.modal-title{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
}
.title-comic{
    padding:5px;
    text-align: justify;
    max-width: 270px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.img-card-cart{
    max-height: 427px;
    
}
.messages {
    position: absolute;
    top:100px;
    right: 20px;
    z-index: 5;
}
* {
    margin: 0 ;
    padding: 0; 
    box-sizing: border-box;
    outline: 0;
    font-family: 'Marvel', sans-serif;
}
